import s from './Pagination.module.scss';
import { useSelector } from 'react-redux';
import { setPageNumb } from '../../redux/paginationSlice';
import { RootState, useAppDispatch } from '../../redux/store';

type PropsType = {
    scrollToItems: () => void;
}


export default function Pagination(props:PropsType) {
    const pages:number = useSelector((state:RootState) => state.pagination.pages);
    let pageNumb:number = useSelector((state:RootState) => state.pagination.pageNumb);
    const dispatch = useAppDispatch();

    return(
        <div className={s.wrapper}>
            <ul>
                <li className={pageNumb == 1 ? s.none : ''}  onClick={() => {
                    props.scrollToItems();
                    pageNumb > 1 &&  dispatch(setPageNumb(--pageNumb));
                }}>
                <svg viewBox="0 0 96 96" xmlns="http://www.w3.org/2000/svg"><title/><path d="M39.3756,48.0022l30.47-25.39a6.0035,6.0035,0,0,0-7.6878-9.223L26.1563,43.3906a6.0092,6.0092,0,0,0,0,9.2231L62.1578,82.615a6.0035,6.0035,0,0,0,7.6878-9.2231Z"/></svg>
                </li>
                {
                    [...new Array(pages)].map((e,index) => <li onClick={()=> {
                        props.scrollToItems();
                        dispatch(setPageNumb(index + 1));
                    }}  className={pageNumb == index+1 ? s.active : ''} key={index}>{index + 1}</li>)
                }
                <li className={pageNumb == pages ? s.none : ''} onClick={() => {
                    props.scrollToItems();
                    pageNumb < pages && dispatch(setPageNumb(++pageNumb));
                    
                }}>
                <svg viewBox="0 0 96 96" xmlns="http://www.w3.org/2000/svg"><title/><path d="M69.8437,43.3876,33.8422,13.3863a6.0035,6.0035,0,0,0-7.6878,9.223l30.47,25.39-30.47,25.39a6.0035,6.0035,0,0,0,7.6878,9.2231L69.8437,52.6106a6.0091,6.0091,0,0,0,0-9.223Z"/></svg>
                </li>
            </ul>
        </div>
    );
}