import { useContext } from 'react';
import s from './Search.module.scss'
import MainContext from '../../context';
import { imgPath } from '../../App';
export default function Search(){
    const state:{
        searchValue:string;
        setSearchValue: any
    } = useContext(MainContext);
    
    return(      
            <div className={s.search} >
               <div>
               <img src={imgPath + "img/search.png"} width='24px' alt="search icon" />
               </div>
                <input onChange={e=> state.setSearchValue(e.target.value)} type="text" placeholder="Поиск..." value={state.searchValue}/>
                <div className={s.clear}><button onClick={() => state.setSearchValue('')}></button></div>
            </div>
    );
}