import { PayloadAction, createSlice } from "@reduxjs/toolkit";
import qs from 'qs';
import { useNavigate } from "react-router-dom";


export type sortType = {
  sortName:string;
  value: string;
}

interface initialStateType {
  categories: string[];
  activeCategory: number;
  sorts: sortType[];
  activeSort:number,
  search: string;
}



const initialState:initialStateType = {
  categories: [
    "Все",
    "Мясные",
    "Вегетарианская",
    "Сырная",
    "Гриль",
    "Острые"
  ],
  activeCategory: 0,
  sorts: [
    {
      sortName: "rating",
      value: "самым популярным",
    },
    {
      sortName: "-rating",
      value: "менее популярным",
    },
    {
      sortName: "price",
      value: "высоким ценам",
    },
    {
      sortName: "-price",
      value: "низким ценам",
    },
    {
      sortName: "-name",
      value: "алфавиту (a-я)",
    },
    {
      sortName: "name",
      value: "алфавиту (я-а)",
    }
  ],
  activeSort: 0,
  search: ''
}


const filterSlice = createSlice({
  name: "filter",
  initialState: initialState,
  reducers: {
    setActive: (state, action:PayloadAction<{index: number; filter:string}>) => {
      (state as any)[action.payload.filter] = action.payload.index;
  
      
    },
    urlSetFilters:(state, action:PayloadAction<{activeCategory: number; sortName:string}>) => {
      state.activeCategory = +action.payload.activeCategory;
      state.activeSort = state.sorts.findIndex(item => item.sortName == action.payload.sortName);
    },
    setSearch: (state, action:PayloadAction<number>) => {

      state.search =  qs.stringify({
        pageNumb:action.payload,
        sortName:state.sorts[state.activeSort].sortName,
        activeCategory: state.activeCategory
      }); 

      
      
    }
  }
});

export const { setActive, urlSetFilters, setSearch } = filterSlice.actions;

export default filterSlice.reducer;
