import { createSlice } from "@reduxjs/toolkit";

interface InitialState{
    pizzaId: number;
    pizzaUrl: string;
    showPopup:boolean;
}

const initialState:InitialState = {
    pizzaId: 0,
    pizzaUrl: '',
    showPopup: false
}

const pizzaPopupSlice = createSlice({
    name: 'pizzaPopup',
    initialState: initialState,
    reducers: {
        setPizzaId: (state, action)=>{
            state.pizzaId = action.payload;
        },
        setPizzaUrl: (state, action)=>{
            state.pizzaUrl = action.payload;
        },
        setShowPopup: (state, action)=>{
            state.showPopup = action.payload;
        }
    }
})

export const {setPizzaId, setPizzaUrl, setShowPopup} = pizzaPopupSlice.actions;

export default pizzaPopupSlice.reducer;