import { imgPath } from "../../App";
import s from "./Page404.module.scss";

type propsType = {
  img: string;
  title: string;
  text: string;
}

const Page404 = (props:propsType)=> {
  return (
    <div className={s.message}>
      <div>
        <img src={imgPath + props.img} alt="" />
        <h2>{props.title}</h2>
        <p>{props.text}</p>
      </div>
    </div>
  );
}

export default Page404;