import s from '../PizzaItem/PizzaItem.module.scss';
import loaderS from './PizzaLoader.module.scss'

export default function PizzaLoader() {

    return(
        <div className={`${s.item} ${loaderS.item}`}>
        <div className={loaderS.img__container}>
          <div className={loaderS.img}>
          <img src='img/item1.png' alt="" className={s.pizza} />
          </div>
        </div>
  
        <h4>Text</h4>
        <div className={`${s.pizza__options} ${loaderS.option}`}>
          <ul>
            <li>trad</li>
          </ul>
          <ul>
              <li>см.</li>
          </ul>
        </div>
        <div className={s.price__cart}>
          <p className={loaderS.price}>от 000 ₽.</p>
          <button className={loaderS.add__cart}>
            <p>+</p>
            <p className={loaderS.text}>Добавить</p>
            <p className={loaderS.count}>0</p>
          </button>
        </div>
      </div>
    )
}