import { Link } from "react-router-dom";
import s from "./Btn.module.scss";

type btnProps = {
  func: boolean;
  text: string
}

export default function Btn(props:btnProps) {
  const isFunc = props.func;

  return (
    <div className={s.btn}>
      {isFunc ? 
          <button className={s.func}>{props.text}</button>
            : 
        <Link to={'/'}>
            <button>{props.text}</button>
        </Link>
      }
    </div>
  );
}
