import { Link, useLocation } from "react-router-dom";
import s from "./Header.module.scss";
import Search from "../Search/Search";
import { useSelector } from "react-redux";
import { RootState } from "../../redux/store";
import { PizzaType } from "../../redux/cartSlice";
import { imgPath } from "../../App";



export default function Header() {
  const cartCount:number = useSelector((state:RootState) => state.cart.items.length);
  const cartTotalSumm:number = useSelector((state:RootState) => state.cart.items.reduce((accum:number, item:PizzaType) => accum + item.price * item.count,0));

  const params = useLocation();
  
  return (
    <header>
      <Link to={'/'}>
      <div className={s.left}>
        <div className={s.logo}>
          <img src={imgPath + "img/logo.svg"} alt="Logo" />
        </div>

        <div className={s.logo__name}>
          <h3>RODO-PIZZA</h3>
          <p>RODO-Пицца: Забудь о всем, кроме вкуса!</p>
        </div>
      </div>
      </Link>
      {
        params.pathname != '/cart'  ? <Search /> : null
      }
      
      { params.pathname != '/cart' &&
        <Link to={'/cart'}>
          <div className={s.right}>
          
            <div className={s.total__summ}>
              <p>{cartTotalSumm} ₽</p>
            </div>
            <div className={s.vertical__line}></div>
            <div className={s.cart__count}>
              <img src={imgPath +"img/cart-icon.svg"} alt="cart Icon" />
              <p>{cartCount}</p>
            </div>
         
          </div>
          </Link>
        }
      
    </header>
  );
}
