import { PayloadAction, createSlice } from "@reduxjs/toolkit";

interface InitialState{
    id:number;
    items:PizzaType[];
    count: number;
}

export type PizzaType = {
    category:number;
    description:string;
    id:number;
    img:string;
    name:string;
    options:string;
    price:number;
    rating:number;
    sizes:number;
    slice:string;
    count:number;
}

const storageCart = localStorage.getItem('cart');

const initialState:InitialState = {
    id:0,
    items:  storageCart ? JSON.parse(storageCart) : [],
    count:0
}


const cartSlice = createSlice({
    name: 'cart',
    initialState:initialState,
    reducers:{
        setItem: (state, action:PayloadAction<PizzaType>) => {

            const findItem = state.items.find((item:PizzaType) => item.options == action.payload.options && item.sizes == action.payload.sizes && item.name == action.payload.name);
            state.id = findItem == undefined ? state.id + 1 : state.id;            
            const obj:PizzaType = {
                ...action.payload,
                id: state.id,
                img: action.payload.img.replace('img/', 'img/cartItems/')
            }

            if(findItem != undefined && findItem.count){
                findItem.count++;
            }else  state.items.push(obj);
        },
        removeItem: (state,action) =>{
            state.items = state.items.filter(item => item.id != action.payload );
        },
        allRemoveItem: (state) =>{
            state.items = [];
           
        },
        changeCount: (state, action) => {

            state.items = state.items.map((item:PizzaType) => {
                if(item.name == action.payload.item.name && item.options == action.payload.item.options && item.sizes == action.payload.item.sizes && item.count) return {...item, count: action.payload.operator == '-' ? --item.count : ++item.count};
                else return item
            })            
        }
    }
})

export const {setItem, changeCount, removeItem, allRemoveItem} = cartSlice.actions;

export default cartSlice.reducer;