import {  useState } from 'react'
import s from './Sort.module.scss'
import { useSelector, useDispatch } from 'react-redux';
import {setActive, sortType} from './../../redux/filterSlice'
import { RootState } from '../../redux/store';

export default function Sort(){
    const [sortOpen, setSortOpen] = useState<boolean>(true);
    const sorts:sortType[] = useSelector((state:RootState) => state.filter.sorts);
    const activeSort:number = useSelector((state:RootState) => state.filter.activeSort);
    const dispatch = useDispatch();
    
    return(
        <div className={s.sort}>
           <div  className={s.sort__display}>
           <p>Сортировка по:</p>
            <p  onClick={()=> setSortOpen(!sortOpen)}>{sorts[activeSort].value}</p>
           
            <div className={`${s.sort__items} ${sortOpen && s.close}`}>
                <ul>
                {sorts.map((item: sortType, index:number) => <li key={index} onClick={()=> {
                    dispatch(setActive({index, filter:'activeSort'}));
                    setSortOpen(!sortOpen);
                }} className={activeSort == index ? s.active : ''}>{item.value}</li>)}
                </ul>
            </div>
            </div>
        </div>
    )
}