import React, { useEffect } from 'react'
import s from './Home.module.scss'
import Categories from '../Categories/Categories'
import Sort from '../Sort/Sort'
import PizzaItem from '../PizzaItem/PizzaItem'
import PizzaLoader from '../PizzaLoader/PizzaLoader'
import Pagination from '../Pagination/Pagination'
import { useSelector } from 'react-redux'
import { RootState, useAppDispatch } from '../../redux/store'
import Page404 from '../Page404/Page404'
import { useParams } from 'react-router-dom'
import { FullPizza } from '../FullPizza/FullPizza'
import { setPizzaId, setPizzaUrl, setShowPopup } from '../../redux/pizzaPopupSlice'



export default function Home(){
    const items = useSelector((state:RootState) => state.pizzaItems.items);
    const loading = useSelector((state: RootState) => state.pizzaItems.loading);
    const scroolElement = React.useRef<HTMLDivElement>(null);
    const pizzaUrl = useSelector((state: RootState) => state.pizzaPopup.pizzaUrl);
    const activePizzaId = useSelector((state:RootState) => state.pizzaPopup.pizzaId);
    const dispatch = useAppDispatch();
    const scrollToItems = () => {
        items.length && scroolElement.current?.scrollIntoView();
    };
   
    const params = useParams()

    
    useEffect(()=>{
        if(window.location.pathname != '/'){
            dispatch(setPizzaUrl(window.location.pathname));
            dispatch(setPizzaId(params.id));
            setTimeout(() => {
                dispatch(setShowPopup(true));
            }, 500);       
            
        }
    },[])

    useEffect(()=>{
        document.body.style.overflow = pizzaUrl == '/pizza/' + activePizzaId + '/' ? 'hidden' : 'auto';
    },[pizzaUrl]);

    return(
        <div className={s.home} ref={scroolElement}>
            <div className={s.sort__categories}>
                <Categories />
                <Sort />
            </div>
           
            {
                <div className={`${s.pizza__wrapper} ${ pizzaUrl == '/pizza/' + activePizzaId + '/' ? s.pizza__show : ''}`}>
                    <FullPizza  id={activePizzaId}/> 
                </div>
            }

            <div className={s.items} >
                {
                    loading == 'error' ?
                    <Page404 img='img/emptyPizza.png' title='Увы, не получилось загрузить пиццы :(' text=''/>
                
                    :
                    loading == 'waiting' ? 
                    [...new Array(4)].map((item,index) => <PizzaLoader key={index} />)
                    : 
                    loading == 'done' && items.length ?
                    items.map((item:any) => <PizzaItem key={item.id}  cartItem={item}/>)
                    :
                    <Page404 img='img/emptyPizza.png' title='Увы, такой пиццы нет :(' text=''/>
                }
            </div>
            {
                items.length ? <Pagination scrollToItems={scrollToItems} /> : null 
            }


           
        </div>
    )
}