import { useSelector } from 'react-redux';
import s from './FullPizza.module.scss';
import { RootState, useAppDispatch } from '../../redux/store';
import { pizza } from '../../redux/pizzaItemsSlice';
import { imgPath } from '../../App';
import { setPizzaUrl, setShowPopup } from '../../redux/pizzaPopupSlice';
import { setSearch } from '../../redux/filterSlice';




export const FullPizza = (props: { id: number }) => {
    const dispatch = useAppDispatch();
    const pizza = useSelector((state:RootState) => state.pizzaItems.items.find((item:pizza) => item.id == props.id));
    const showPopup = useSelector((state:RootState) => state.pizzaPopup.showPopup);
    const pageNumb = useSelector((state:RootState) => state.pagination.pageNumb);
    const search = useSelector((state:RootState) => state.filter.search);
    
    const closePopup = () =>{        
        window.history.pushState(null,'', '/?'+search);
        dispatch(setShowPopup(false));

        setTimeout(() => {
            dispatch(setPizzaUrl('/'));
            dispatch(setSearch(pageNumb));
          }, 500);
    }

    
   
    return (

        <div className={`${s.container} ${showPopup ? s.content__show : ''}`}>
            
            <div className={s.pizza}>
            <button className={s.close} onClick={closePopup}></button>
                <div className={s.pizza__img}>
                    <img src={imgPath + pizza?.img.replace('img/', 'img/cartItems/')} alt="Pizza" />
                </div>
                <div className={s.descriptions}>
                    <h2>{pizza?.name}</h2>
                    <p>{pizza?.description}</p>
                    <h2>{pizza?.name}</h2>
                    <p>{pizza?.description}</p> <h2>{pizza?.name}</h2>
                    <p>{pizza?.description}</p> <h2>{pizza?.name}</h2>
                    <p>{pizza?.description}</p> <h2>{pizza?.name}</h2>
                    <p>{pizza?.description}</p> <h2>{pizza?.name}</h2>
                    <p>{pizza?.description}</p>
                </div>
            </div>
        </div>

    );
}