import { useDispatch } from "react-redux";
import s from "./CartItem.module.scss";
import { PizzaType, changeCount, removeItem } from "../../redux/cartSlice";

type cartProps = {
  item: PizzaType;
}



export default function CartItem<PizzaType>(props:cartProps) {
  const dispatch = useDispatch();

  return (
    <div className={s.item}>
      <div className={s.title}>
        <img src={props.item.img} alt="" />
        <div>
          <h4>{props.item.name}</h4>
          <p>{`${props.item.options} тесто, ${props.item.sizes}см.`}</p>
        </div>
      </div>
      <div className={s.count}>
        <button disabled={props.item.count == 1} onClick={()=> {
          dispatch(changeCount({
          item: props.item,
          operator: '-'
        })) 
        }}>-</button>
        <h4>{props.item.count}</h4>
        <button onClick={()=> dispatch(changeCount({
          item: props.item,
          operator: '+'
        }))}>+</button>
      </div>
      <div className={s.price}>
        <p>{props.item.price * props.item.count}р</p>
      </div>
      <button className={s.delete} onClick={() => {
        dispatch(removeItem(props.item.id))
      }}>X</button>
    </div>
  );
}
