import s from './Categories.module.scss'
import { useSelector, useDispatch } from 'react-redux';
import {setActive} from '../../redux/filterSlice'
import { setPageNumb } from '../../redux/paginationSlice';



export default function Categories(){
    const categories = useSelector((state:any) => state.filter.categories);
    const activeCategory = useSelector((state:any) => state.filter.activeCategory);
    const dispatch = useDispatch();
    return(
        <div className={s.categories}>
                <ul>
                    {
                        categories.map((item:any,index:number) => <li key={index} onClick={() => {
                            dispatch(setActive({index, filter:'activeCategory'}));
                            dispatch(setPageNumb(1));
                        }} className={activeCategory == index ? s.active : ''}>{item}</li>)
                    }
                </ul>
        </div>
    )
}