import { useEffect, useRef, useState } from "react";
import s from "./PizzaItem.module.scss";
import { useDispatch, useSelector } from "react-redux";
import { PizzaType, setItem } from "../../redux/cartSlice";
import { RootState } from "../../redux/store";
import { imgPath } from "../../App";
import { setPizzaId, setPizzaUrl, setShowPopup } from "../../redux/pizzaPopupSlice";
import qs from 'qs';
import { setSearch } from "../../redux/filterSlice";

type pizzaItemProps = {
  cartItem: {
    id: number;
    img: string;
    slice: string;
    name: string;
    description: string;
    options: number | string;
    sizes: number[];
    price: number[];
    rating: number;
    category: number;
  }
}

export default function PizzaItem(props: pizzaItemProps) {
  const cartItems = useSelector((state:RootState) => state.cart.items);
  const [optionState, setOptionState] = useState<number>(0);
  const [sizeState, setSizeState] = useState<number>(1);
  const pizzaOption: string[] = useSelector((state:RootState) => state.pizzaItems.pizzaOptions);
  const sizes: number[] = useSelector((state:RootState) => state.pizzaItems.sizes);
  const pizzaCount: number = useSelector((state: RootState) => state.cart.items.find((item: PizzaType) => item.name == props.cartItem.name && item.options == pizzaOption[optionState] && item.sizes == sizes[sizeState])?.count || 0);
  const pageNumb = useSelector((state:RootState) => state.pagination.pageNumb);
  const activeSort = useSelector((state: RootState) => state.filter.sorts[state.filter.activeSort]);
  const activeCategory = useSelector((state: RootState) => state.filter.activeCategory);
  const dispatch = useDispatch();
  const smoke = useRef<HTMLDivElement>(null);
  const isMounted = useRef(false);
  
  useEffect(() =>{
    if(isMounted.current){
      localStorage.setItem('cart', JSON.stringify(cartItems));
    }
    isMounted.current = true;
  }  ,[cartItems]);
  
  
  const getRandom = (min: number, max: number) => {
    return Math.floor(Math.random() * (max - min + 1)) + min;
  }
  const [smokeSpan, setSmokeSpan] = useState<number[]>([]);
  const newSmokeSpan: number[] = [];

  const [isEnd, setisEnd] = useState<boolean>(true)



  for (let i = 0; i <= 25; i++) {
    newSmokeSpan.push(getRandom(0, 25))
  }

  const setOptionHandler = (index: number) => {
    if(isEnd) {
      setOptionState(index);
      setSizeState((prev:number) => index == 1 ? (prev >= 1 ? prev : 1) : prev );
    }
  }
  
  const sizesHandler = async (index: number) => {
    if(optionState == 1 && index == 0) return;
   if(isEnd) {
    setisEnd(false);
    setSizeState(index);
    setSmokeSpan(newSmokeSpan);
    
    smoke.current?.classList.add(s.show__smoke);
     await new Promise<void>(res => {
     setTimeout(() => {
        res(smoke.current?.classList.remove(s.show__smoke)); 
            
      }, 2000)
    });
  
    await new Promise<void>(res => {
     setTimeout(() => {
        res( setSmokeSpan([]));   
        setisEnd(true)  
      }, 300)
    }); 
   }
  };

  const showFullPizza = (id:number) =>{
    dispatch(setSearch(pageNumb))

    const search = qs.stringify({
      pageNumb,
      sortName: activeSort.sortName,
      activeCategory
    })

    window.history.pushState(null,'',`/pizza/${id}/?${search}`);
    dispatch(setPizzaId(id));
    dispatch(setPizzaUrl(window.location.pathname));

    setTimeout(() => {
      dispatch(setShowPopup(true));      
    }, 500);
  }


  return (
    <div className={s.item}>
     
     <div className={s.img__container}>
     
        <div className={s.img__wrapper} onClick={()=>showFullPizza(props.cartItem.id)}>
          <div className={`${s.img} ${!sizeState ? s.small : ''} ${sizeState == 2 ? s.large : ''}`}>
            <img src={imgPath + props.cartItem.slice} alt="" className={s.slice} />
            <img src={imgPath + props.cartItem.img} alt="" className={s.pizza} />
          </div>
          <div ref={smoke} className={s.smoke}>
            {
              smokeSpan.map((item: number, index: number) => <span key={index} style={{ animationDelay: item * -0.5 + "s" }}></span>)
            }
          </div>
        </div>
        <div className={s.description}>
          <p>{props.cartItem.description}</p>
        </div>
       
      </div>
   

      <h4>{props.cartItem.name}</h4>
      <div className={s.pizza__options}>
        <ul>
          {pizzaOption.map((item, index) => (
            <li
              key={index}
              onClick={() => setOptionHandler(index)}
              className={optionState == index ? s.active : ""}
            >
              {item}
            </li>
          ))}
        </ul>
        <ul>
          {sizes.map((item, index) => (
            <li
              key={index}
              className={`${sizeState == index ? s.active : ""}  ${optionState == 1 && index == 0 ? s.size__none : ''}`}
              onClick={() => sizesHandler(index)}
            >
              {item} см.
            </li>
          ))}
        </ul>
      </div>
      <div className={s.price__cart}>
        <p className={s.price}>от {props.cartItem.price[sizeState]} ₽.</p>
        <button className={s.add__cart} onClick={() => {
          dispatch(setItem(
            {
              ...props.cartItem,
              options: pizzaOption[optionState].toString(),
              sizes: Number(sizes[sizeState]),
              count:1,
              price: props.cartItem.price[sizeState]
            }
          ));
        }}>
          <p className={s.plus}>+</p>
          <p className={s.text}>Добавить</p>
          <p className={s.count}>{pizzaCount}</p>
        </button>
      </div>
    </div>
  );
}



