import React, { useEffect, useRef, useState } from "react";
import qs from 'qs';
import "./App.css";
import Header from "./components/Header/Header";
import Home from "./components/Home/Home";
import MainContext from "./context";
import { Route, Routes, useNavigate } from "react-router-dom";
import Cart from "./components/Cart/Cart";
import Page404 from "./components/Page404/Page404";
import { useSelector } from "react-redux";
import { urlSetFilters } from "./redux/filterSlice";
import { setPageNumb } from "./redux/paginationSlice";
import { RootState, useAppDispatch } from "./redux/store";
import { getPizzas } from "./redux/pizzaItemsSlice";
export  const imgPath = process.env.PUBLIC_URL + '/';


function App() {
  const renderFlag = useRef(false);
  const activeCategory = useSelector((state: RootState) => state.filter.activeCategory);
  const activeSort = useSelector((state: RootState) => state.filter.sorts[state.filter.activeSort]);
  const pageNumb = useSelector((state: RootState) => state.pagination.pageNumb)
  const dispatch = useAppDispatch();
  const [searchValue, setSearchValue] = useState<string>('');
  const navigate = useNavigate();
  const [isInitialized, setInitialized] = useState(false);


  let sortName = activeSort.sortName.replace('-', '');
  let order = activeSort.sortName.includes('-') ? 'asc' : 'desc';

  type ulrParamsType = {
    activeCategory: number;
    pageNumb:number;
    sortName:string
  }

  const getData = () => {
    dispatch(getPizzas({
      sortName,
      order,
      pageNumb,
      activeCategory,
      searchValue
    })); 
  }
  


useEffect(() => {
    if (window.location.search) {
      const urlParams: ulrParamsType = (qs.parse(window.location.search.substring(1)) as unknown) as ulrParamsType;
      dispatch(urlSetFilters(urlParams));
      dispatch(setPageNumb(+urlParams.pageNumb));

      
    }
    setInitialized(true);
}, []);

useEffect(() => {  
  if (isInitialized) {
    if (renderFlag.current) {
      const queryString = qs.stringify({
        pageNumb,
        sortName,
        activeCategory,
      });
      navigate('/?' + queryString);
    }
    renderFlag.current = true;
   
      getData();
   
  }
}, [isInitialized, activeSort, activeCategory, searchValue, pageNumb]);



  const contextValues = {
    searchValue,
    setSearchValue,
  };

  

  return (
    <MainContext.Provider value={contextValues}>
      <div className="app">
        <div className="container">
          <Header />
          <Routes>
            <Route path="/pizza?/:id?" element={<Home />} />
            <Route path="/cart" element={<Cart />} />
            <Route path="*" element={<Page404  img='img/parrot404.png' title="Ничего не найдено" text="К сожалени данная страница отсутствует в нашем интернет-магазине"/>} />
          </Routes>
        </div>
      </div>
    </MainContext.Provider>
  );
}

export default App;
