import { PayloadAction, createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import { setPages } from "./paginationSlice";

export type pizza =  {
    id: number;
    img: string;
    slice: string;
    name: string;
    description: string;
    options: number;
    sizes: number[];
    price: number[];
    rating: number;
    category:number;
}

interface InitialState{
    pizzaOptions: string[];
    sizes: number[];
    items: pizza[];
    loading: string;
   
}



export interface SearchParams{
    sortName:string;
    order:string;
    pageNumb:number;
    activeCategory:number ;
    searchValue:string;
}

export const getPizzas = createAsyncThunk<pizza[], SearchParams>('pizza/getItems', async (params, asyncAPI) =>{

    const {sortName, order, pageNumb, activeCategory, searchValue} = params;
    try {
        const res = await fetch(`https://653f60ac9e8bd3be29e06f12.mockapi.io/items?page=${pageNumb}&limit=4${searchValue ? '&name=' + encodeURIComponent(searchValue) : ''}${activeCategory != 0 ? '&category=' + activeCategory : ''}&sortBy=${sortName}&order=${order}`, {
        method: "GET",
      })

      if(res.status != 404 && res.ok){
        const data = await res.json();
        asyncAPI.dispatch(setPages(Math.ceil(10 / 4)))
        console.log(res.headers.get('X-Requested-With'));
        
        return data;
      }else throw new Error;
      
    } catch (error) {
        throw error
    }
    
})



const initialState: InitialState  = {
    pizzaOptions: ["традиционное", "тонкое"],
    sizes: [26, 30, 40],
    items: [],
    loading: 'waiting'
}

const pizzaitemsSlice = createSlice({
    name: 'pizzaItems',
    initialState,
    reducers: {
     
    },
    extraReducers: (builder)=>{
        builder.addCase(getPizzas.pending, (state, action) =>{
            state.items = [];
            state.loading = 'waiting'
        });

        builder.addCase(getPizzas.fulfilled, (state, action) =>{
           
            state.items = action.payload;
            state.loading = 'done'
        });

        builder.addCase(getPizzas.rejected, (state, action) =>{
            state.items = [];
            state.loading = 'error';
            console.log(action.error);
            
            alert(action.error.message)
        });
    }
});

 

export default pizzaitemsSlice.reducer;