import { useDispatch, useSelector } from "react-redux";
import Btn from "../Btn/Btn";
import CartItem from "../CartItem/CartItem";
import s from "./Cart.module.scss";
import { PizzaType, allRemoveItem } from "../../redux/cartSlice";
import { RootState } from "../../redux/store";
import { useEffect } from "react";



const Cart = ()=> {
  const cart:PizzaType[] = useSelector((state:RootState) => state.cart.items);
  
  const totalSumm:number = useSelector((state:RootState) => state.cart.items.reduce((accum:number, item:PizzaType) => item.count ? accum + item.price * item.count : 0,0));
  const pizzaCount:number = useSelector((state:RootState) => state.cart.items.reduce((accum:number , item:PizzaType)=> item.count ? accum + item.count : 0,0))
  const dispatch = useDispatch();

  useEffect(()=>{
    window.localStorage.setItem('cart',JSON.stringify(cart))
  },[cart]);
  
  
  return (
    <>
      {cart.length == 0 ? 
        <div className={s.empty}>
          <h3>Корзина пустая </h3>
          <p>
            Вероятней всего, вы не заказывали ещё пиццу. Для того, чтобы
            заказать пиццу, перейди на главную страницу.
          </p>
          <img src="img/empty-cart.png" alt="" />
          <Btn text="Вернуться назад" func={false} />
        </div>
       : 
        <div className={s.wrapper}>
          <div className={s.header}>
            <div className={s.title}>
              <img src="img/cart-icon2.svg" alt="" />
              <h3>Корзина</h3>
            </div>

            <div className={s.clear} onClick={()=> {
               window.confirm('Точно хотите отчистить корзину?') && dispatch(allRemoveItem())
            }}>
              <img src="img/clear-icon.svg" alt="" />
              <p>Очистить корзину</p>
            </div>
          </div>

          <div className={s.items}>
            {
              cart.map((elem:PizzaType) => <CartItem key={elem.id} item={elem} />)
            }
          </div>
         
            <div className={s.total}>
              <p>
                Всего пицц: <b>{pizzaCount} шт.</b>
              </p>
              <div>
                <p>
                  Сумма заказа: <b className={s.total__summ}>{totalSumm} ₽</b>
                </p>
                <Btn text="Оплатить сейчас" func />
              </div>
            </div>
         
        </div>
      }
    </>
  );
}


export default Cart;